@import "./variables";

.el-container {
  flex-wrap: wrap;
}

.static {
  &-banner {
    min-height: 610px;
    padding: 63px 0;
    background-image: url("../../img/main-banner.jpg");
    background-size: cover;
    background-position: center left;
    background-repeat: no-repeat;
    @media (max-width: 767px) {
      text-align: center;
      height: auto;
    }
    &__title {
      width: 100%;
      margin-bottom: 28px;
      line-height: 1.2;
      font-size: 30px;
      font-weight: 400;
      color: #fff;
    }
    &__text {
      max-width: 50%;
      color: #fff;
      font-size: 16px;
      line-height: 1.8;
      @media (max-width: 767px) {
        max-width: 100%;
      }
    }
    &__button {
      display: inline-block;
      align-self: flex-start;
      padding: 14px 58px;
      margin-top: 50px;
      border: none;
      border-radius: 3px;
      background-color: $secondaryC2;
      font-size: 25px;
      color: #fff;
      transition: 0.4s;
      &:hover {
        background-color: lighten($secondaryC2, 8%);
      }
      @media (max-width: 767px) {
        align-self: center;
      }
    }
    p:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &-about {
    &__cards {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      padding-top: 60px;
      padding-bottom: 30px;
      @media (max-width: 767px) {
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 50px;
        padding-bottom: 0;
      }
      &-wp {
        background-color: #efefef;
      }
    }
    &__card {
      max-width: 30%;
      margin: 0 10px;
      text-align: center;
      @media (max-width: 767px) {
        max-width: 80%;
        margin-bottom: 40px;
      }
    }
    &__img {
      width: 95px;
      display: inline-block;
      height: 80px;
      margin-bottom: 30px;
      background-position: center;
      background-repeat: no-repeat;
      &_ico1 {
        background-image: url("data:image/png;base64,");
      }
      &_ico2 {
        background-image: url("data:image/png;base64,");
      }
      &_ico3 {
        background-image: url("data:image/png;base64,");
      }
    }
    &__text {
      font-size: 13px;
    }
    &-description {
      background: #fff;
      margin-bottom: -20px;
    }
    &__sub {
      &-title {
        width: 70%;
        margin-bottom: 30px;
        margin-left: auto;
        margin-right: auto;
        padding-top: 50px;
        text-align: justify;
        @media (max-width: 767px) {
          width: 100%;
        }
      }
      &-text {
        width: 70%;
        margin-bottom: 100px;
        margin-left: auto;
        margin-right: auto;
        text-align: justify;
        @media (max-width: 767px) {
          width: 100%;
          margin-bottom: 50px;
          text-align: center;
        }
      }
    }
    &-partners {
      width: 100%;
      padding-bottom: 50px;
      @media (max-width: 767px) {
        padding-bottom: 30px;
      }
      .static-about {
        &__title {
          margin-bottom: 50px;
          text-align: center;
          font-size: 18px;
          font-weight: 700;
          color: $secondaryC1;
        }
        &__links {
          display: flex;
          justify-content: space-between;
          align-items: center;
          @media (max-width: 767px) {
            justify-content: center;
            flex-wrap: wrap;
          }
          a {
            width: 20%;
            @media (max-width: 768px) {
              min-width: 200px;
              max-width: 50%;
              margin: 20px;
            }
          }
        }
      }
    }
    .static-about__line {
      display: block;
      width: 70%;
      height: 1px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 50px;
    }
    &-procurement {
      text-align: center;
      .static-about {
        &__title {
          margin-bottom: 120px;
          text-align: center;
          font-size: 30px;
          font-weight: 700;
          line-height: 24.38px;
          @media (max-width: 640px) {
            margin-bottom: 50px;
          }
        }
        &__items {
          margin-bottom: 80px;
          @media (max-width: 640px) {
            margin-bottom: 30px;
          }
          @media (max-width: 640px) {
            padding-left: 15px;
          }
          .item6 {
            border: 2px solid #fff;
          }
        }
        &__item {
          &_left,
          &_right {
            display: block;
            width: 50%;
            margin-top: -50px;
            @media (max-width: 640px) {
              width: 100%;
            }
            .date_left:after,
            .date_right:after {
              content: "";
              position: absolute;
              top: 0;
              display: block;
              width: 15px;
              height: 15px;
              background-color: #2383c5;
              transform: rotate(45deg);
              outline: 5px solid #fff;
            }
            .date_left:after {
              right: -53px;
              @media (max-width: 640px) {
                left: -53px;
              }
            }
            .date_right:after {
              left: -53px;
            }
          }
          &_right {
            padding-left: 45px;
            transform: translateX(99.8%);
            text-align: left;
            border-left: 1px solid #2383c5;
            @media (max-width: 640px) {
              margin-top: 0;
              transform: translateX(0);
            }
          }
          &_left {
            padding-right: 45px;
            border-right: 1px solid #2383c5;
            text-align: right;
            @media (max-width: 640px) {
              margin-top: 0;
              padding-right: 0;
              padding-left: 45px;
              text-align: left;
              border-right: 0;
              border-left: 1px solid #2383c5;
            }
          }
          &-date {
            position: relative;
            font-size: 14px;
            color: #2383c5;
          }
          &-title {
            padding-bottom: 10px;
            font-size: 20px;
            line-height: 1.5;
            &_accent {
              padding-bottom: 10px;
              line-height: 1.6;
              font-size: 24px;
              font-weight: 700;
            }
          }
          &-text {
            font-size: 14px;
            @media (max-width: 640px) {
              padding-bottom: 20px;
            }
          }
        }
        &__text {
          display: inline-block;
          width: 70%;
          margin-bottom: 110px;
          text-align: center;
          font-size: 15px;
          @media (max-width: 640px) {
            width: 100%;
            margin-bottom: 50px;
          }
        }
      }
    }
  }
  &-join {
    &__cards {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      padding-top: 60px;
      padding-bottom: 30px;
      @media (max-width: 767px) {
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 50px;
        padding-bottom: 0;
      }
    }
    &__card {
      max-width: 20%;
      margin: 0 10px;
      text-align: center;
      @media (max-width: 767px) {
        max-width: 80%;
        margin-bottom: 40px;
      }
    }
    &__img {
      width: 95px;
      display: inline-block;
      height: 80px;
      margin-bottom: 20px;
      background-position: center;
      background-repeat: no-repeat;
      &_ico1 {
        background-image: url("data:image/png;base64,");
      }
      &_ico2 {
        background-image: url("data:image/png;base64,");
      }
      &_ico3 {
        background-image: url("data:image/png;base64,");
      }
      &_ico4 {
        background-image: url("data:image/png;base64,");
      }
    }
    &__title {
      margin-bottom: 20px;
      font-size: 16px;
      font-weight: 700
    }
    &__text {
      font-size: 13px;
    }
  }
  &-for-civil {
    width: 100%;
    padding-top: 50px;
    text-align: justify;
  }
  &-security {
    text-align: justify;
    padding-top: 50px;
    h1 {
      text-align: center;
    }
    &__steps{
      ol {
        margin-bottom: 30px;
      }
      li {
        margin-bottom: 30px;
        text-align: left;
      }
    }
    &__decisions{

      li {
        list-style-type: lower-alpha;
        text-align: left;
        margin-bottom: 10px;
        margin-left: 50px;
      }
    }

    h3{
      margin-top: 50px;
      text-align: center;
    }
  }
  h1 {
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 700;
  }
  h3 {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 700;
  }
  p:not(:last-child) {
    margin-bottom: 10px;
  }
  strong {
    font-weight: 700;
  }
  .underline {
    text-decoration: underline;
  }
  .nowrap {
    white-space: nowrap;
  }
  a {
    &:hover {
      text-decoration: underline;
    }
  }
  li > a {
    color: #fff;
    &:visited {
      color: #fff;
    }
  }
  ul {
    margin-bottom: 20px;
    list-style-position: inside;
  }
  ul > li {
    list-style-type: disc;
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }
  hr {
    display: block;
    width: 70%;
    height: 1px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
  }
  .organizations {
    display: flex;
    justify-content: space-between;
    @media (max-width: 767px) {
      flex-wrap: wrap;
     justify-content: space-around;
    }
    &-item {
      width: calc(100% / 6);
      margin: 20px auto 0;
      text-align: center;
      @media (max-width: 767px) {
        width: calc(100% / 3);
      }


      &__img {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 90px;
      }
      &__name {
        text-align: center;
        font-size: 14px;
        p{
        }
      }
    }
  }
}

.main-news {
  padding-top: 20px;
  padding-bottom: 52px;
  &__title {
    margin-bottom: 18px;
    font-size: 30px;
    font-weight: 700;
  }
  &__cards-wp {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 940px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__card {
    max-width: 289px;
    margin: 0 20px;
    @media (max-width: 940px) {
      margin: 20px;
    }
  }
  &__img {
    margin-bottom: 22px;
  }
  &__text {
    line-height: 1.65;
    font-size: 13px;
  }
}
